.sidebar {
  padding: 10px; }
  .sidebar ul, .sidebar li {
    padding: 0; }
  .sidebar li.active, .sidebar li.active > a {
    font-weight: 600; }

.Card {
  box-shadow: rgba(60, 64, 67, 0.08) 0px 1px 1px 0px, rgba(60, 64, 67, 0.16) 0px 1px 3px 1px;
  border-radius: 0 6px 6px 0;
  display: inline-block;
  margin: 10px;
  width: 100%;
  max-width: 400px;
  display: flex;
  flex-direction: row;
  justify-content: stretch;
  align-content: stretch;
  align-items: stretch; }
  .Card .text {
    padding: 5px; }

.AlbumCard .text {
  display: flex;
  flex-direction: column;
  justify-content: space-between; }
  .AlbumCard .text h1 {
    margin: 0;
    font-size: 22px; }
  .AlbumCard .text p {
    margin: 0;
    font-size: 16px; }
  .AlbumCard .text .links a {
    display: flex;
    align-content: flex-end;
    align-items: flex-end; }
    .AlbumCard .text .links a img {
      height: 26px;
      width: 26px;
      margin-right: 4px; }
    .AlbumCard .text .links a span {
      padding-bottom: 2px; }

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
