.App-header {
  margin: 0;
  padding: 10px;
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  background-color: white;
  width: 100%;
  z-index: 5;
  box-sizing: border-box; }
  .App-header h1 {
    margin: 0; }

.App > .layout {
